<script>

export default {
  name: "app",
  methods: {
    parallax(items){
      let parallax = (item) =>{
        item.style.transform = "translateY(" + window.scrollY / -(item.dataset.parallax) + "px)";
      }

      for (let i = items.length - 1; i >= 0; i--) {
        window.addEventListener("scroll", ()=>{
          parallax(items[i])
        }, true)
      }
    }
  },
  mounted(){
    let parallax_items = this.$refs.header.querySelectorAll("[data-parallax]");
    this.parallax(parallax_items)
  }
};
</script>

